  fill: #bcbcbc;
}

body {
  background: #fff;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  outline: none;
  border: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.btn {
