.form {
	box-shadow: 0 10px 49px rgba(28, 28, 28, 0.15);
	border-radius: 3px;
	background-color: rgba(255, 254, 254, 0.48);
	background-image: linear-gradient(to bottom, #ffffff 0%, #ffffff 40%, rgba(255, 255, 255, 0) 100%);
	color: $gray;
	padding: 30px 20px 20px;
	&__in {
		display: flex;
		justify-content: center;
		margin-top: 27px;
		font-size: 14px; }
	&__text {
		line-height: 20px;
		&:not(:first-child) {
			margin-top: 24px; } }
	&__col {
		flex: 1;
		&:last-of-type {
			margin: 4px 0 0 30px; } }
	&__row {
		margin-bottom: 9px; }
	&__field,
	&__textarea {
		border-radius: 3px;
		padding: 9px 13px;
		color: #939393;
		background-color: $bg_gray;
		@extend %inputs;
		font-size: 14px; }
	&__textarea {
		min-height: 69px;
		resize: none;
		letter-spacing: 0.35px; }
	&__footer {
		text-align: center;
		margin-top: 13px; }
	&__btn {
		font-size: 12px;
		letter-spacing: 0.6px; } }
