.b-404 {
	background-color: $bg_gray;
	padding: 25px 0 54px 0;
	&__inner {
		max-width: 950px; }
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 46px; }
	&__col {
		&:first-of-type {
			max-width: 310px; }
		&:last-of-type {
			padding-top: 15px; } }
	&__title {
		font-size: 72px;
		color: #bcbcbc;
		@include bold;
		span {
			display: block;
			font-size: 170px;
			line-height: 140px;
			text-transform: uppercase; } }
	&__subtitle {
		font-size: 14px;
		padding: 21px 0 20px 6px;
		color: $black; }
	&__form {
		position: relative;
		max-width: 208px; }
	&__search {
		@include regular;
		font-style: italic;
		color: #d6d6d6;
		letter-spacing: 0.35px;
		height: 33px;
		line-height: 33px;
		padding: 11px 36px 11px 13px;
		@extend %inputs; }
	&__btn {
		@include hide-text;
		background: url(data:image/png;base64,) center no-repeat;
		width: 19px;
		height: 19px;
		position: absolute;
		top: 46%;
		transform: translateY(-50%);
		right: 9px; } }
